<template>
    <div id="new-fighter">
        <h3>New Fighter</h3>
        <div id="row">
            <form @submit.prevent="saveFighter" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="fighterid" required>
                        <label>Fighter ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="weight" required>
                        <label>Weight</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="age" required>
                        <label>Age</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="affiliation" required>
                        <label>Affiliation</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="affbranch" required>
                        <label>Affiliation Branch</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="belt" required>
                        <label>Belt</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name:'new-fighter',
    data(){
        return{
            fighterid: null,
            name: null,
            weight: null,
            age: null,
            affiliation: null,
            affbranch: null,
            belt: null
        }
    },

    methods:{
        saveFighter(){
            db.collection('fighters').add({
                fighter_id: this.fighterid,
                full_name: this.name,
                weight: this.weight,
                age: this.age,
                affiliation: this.affiliation,
                aff_branch: this.affbranch,
                belt: this.belt
            })
            .then(docRef => this.$router.push('./'))
            .catch(err => console.log(err))
        }
    }
}
</script>




