import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewFighter from '@/components/NewFighter'
import ViewFighter from '@/components/ViewFighter'
import EditFighter from '@/components/EditFighter'
import Login from '@/components/Login';
import Register from '@/components/Register';
import firebase from 'firebase';

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta:{
        requiresAuth:true
      }
    },
    {
      path:'/login',
      name: 'login',
      component: Login,
      meta:{
        requiresGuest:true
      }
    },
    {
      path:'/register',
      name: 'register',
      component: Register,
      meta:{
        requiresGuest:true
      }
    },
    {
      path: '/:fighter_id',
      name: 'view-fighter',
      component: ViewFighter,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/new',
      name: 'new-fighter',
      component: NewFighter,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/edit/:fighter_id',
      name: 'edit-fighter',
      component: EditFighter,
      meta:{
        requiresAuth:true
      }
    },
    
  ]
});

// Nav guards
router.beforeEach((to, from, next) => {
  //Check for requireAuth
  if(to.matched.some(record => record.meta.requiresAuth)){
    //check if Not logged in
    if(!firebase.auth().currentUser){
      //Go to login
      next({
        path: '/login',
        query: {
          redirect:to.fullPath
        }
      });
    }else{
      //Proceed
      next();
    }
  } else if(to.matched.some(record => record.meta.requiresGuest)){
    //check if logged in
    if(firebase.auth().currentUser){
      //Go to login
      next({
        path: '/',
        query: {
          redirect:to.fullPath
        }
      });
    }else{
      //Proceed
      next();
    }
  } else{
    next();
  }
});

export default router;



