<template>
    <div id="edit-fighter">
        <h3>Edit Fighter</h3>
        <div id="row">
            <form @submit.prevent="updateFighter" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="fighterid" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="weight" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="age" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="affiliation" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="affbranch" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="belt" required>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name:'edit-fighter',
    data(){
        return{
            fighterid: null,
            name: null,
            weight: null,
            age: null,
            affiliation: null,
            affbranch: null,
            belt: null
        }
    },

    beforeRouteEnter(to, from, next){
        db.collection("fighters").where('fighter_id', '==', to.params.fighter_id)
        .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.fighterid = doc.data().fighter_id
                    vm.name = doc.data().full_name
                    vm.weight = doc.data().weight
                    vm.age = doc.data().age
                    vm.affiliation = doc.data().affiliation
                    vm.affbranch = doc.data().aff_branch
                    vm.belt = doc.data().belt

                })
            })
        })
    },
    watch:{
        '$route': 'fetchData'
    },
    methods:{
        fetchData(){
            db.collection('fighters').where
            ('fighter_id', '==',
            this.$route.params.fighter_id).get().
            then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.fighterid = doc.data().fighter_id
                    this.name = doc.data().full_name
                    this.weight = doc.data().weight
                    this.age = doc.data().age
                    this.affiliation = doc.data().affiliation
                    this.aff_branch = doc.data().aff_branch
                    this.belt = doc.data().belt
                })
            })
        },

        updateFighter(){
            db.collection('fighters').where
            ('fighter_id', '==',
            this.$route.params.fighter_id).get().
            then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        fighter_id: this.fighterid,
                        full_name: this.name,
                        weight: this.weight,
                        age: this.age,
                        affiliation: this.affiliation,
                        aff_branch: this.affbranch,
                        belt: this.belt
                    })
                    .then(()=>{
                        this.$router.push({name: 'view-fighter', params:{fighter_id: this.fighterid}})
                    })
                })
            })
        }
    }
}
</script>




