<template>
    <div id="view-fighter">
        <ul class = "collection with-header">
            <li class = "collection-header">
            <h4>{{name}}</h4></li>
            <li class="collection-item"><div class="chip">Fighter ID</div> {{fighterid}}</li>
            <li class="collection-item"><div class="chip">Weight</div> {{weight}}</li>
            <li class="collection-item"><div class="chip">Age</div>{{age}}</li>
            <li class="collection-item"><div class="chip">Affiliation</div>{{affiliation}}</li>
            <li class="collection-item"><div class="chip">Affiliation Branch</div>{{affbranch}}</li>
            <li class="collection-item"><div class="chip">Belt</div>{{belt}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteFighter" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-fighter', params:{fighter_id:fighterid}}" class="btn-floating btn-large red">
            <i class="fa fa-pencil"></i>
            </router-link>

        </div>
    </div>
</template>


<script>
import db from './firebaseinit'
export default {
    name:'view-fighter',
    data(){
        return{
            
                fighterid: null,
                name: null,
                weight: null,
                age: null,
                affiliation: null,
                affbranch: null,
                belt: null
        }
    },
    beforeRouteEnter(to, from, next){
        db.collection("fighters").where('fighter_id', '==', to.params.fighter_id)
        .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.fighterid = doc.data().fighter_id
                    vm.name = doc.data().full_name
                    vm.weight = doc.data().weight
                    vm.age = doc.data().age
                    vm.affiliation = doc.data().affiliation
                    vm.affbranch = doc.data().aff_branch
                    vm.belt = doc.data().belt

                })
            })
        })
    },
    watch:{
        '$route': 'fetchData'
    },
    methods:{
        fetchData(){
            db.collection('fighters').where
            ('fighter_id', '==',
            this.$route.params.fighter_id).get().
            then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.fighterid = doc.data().fighter_id
                    this.name = doc.data().full_name
                    this.weight = doc.data().weight
                    this.age = doc.data().age
                    this.affiliation = doc.data().affiliation
                    this.aff_branch = doc.data().aff_branch
                    this.belt = doc.data().belt
                })
            })
        },

        deleteFighter(){
            if(confirm('Are you sure?')){
                db.collection('fighters').where
                ('fighter_id', '==',
                this.$route.params.fighter_id).get().
                then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                    })
                })
            }
        }
    }
}
</script>




