<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Fighters</h4></li>
            <li v-for="fighter in fighters" v-bind:key="fighter.id" class="collection-item">
               
               <div class="chip">{{fighter.affbranch}}</div>
               {{fighter.fighterid}}:{{fighter.name}}
               <router-link v-bind:to="{name: 'view-fighter', params:{fighter_id: fighter.fighterid}}" 
               class="secondary-content"><i class="fa fa-eye"></i></router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
            <i class="fa fa-plus"></i>
            </router-link>

        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name:'dashboard',
    data(){
        return{
            fighters: []
        }
    },
    created(){
        db.collection('fighters').orderBy('fighter_id').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc =>{
                console.log(doc.data());
                const data = {
                    'id': doc.id,
                    'fighterid': doc.data().fighter_id,
                    'name': doc.data().full_name,
                    'weight': doc.data().weight,
                    'age': doc.data().age,
                    'affiliation': doc.data().affiliation,
                    'affbranch': doc.data().aff_branch,
                    'belt': doc.data().belt
                }
                
                this.fighters.push(data);

                
            })
        })
    }
}
</script>




